        var atom = me.icn3d.getFirstCalphaAtomObj(me.icn3d.chains[chainid]);
        var oneLetterRes = me.icn3d.residueName2Abbr(atom.resn.substr(0, 3));

        $("#" + me.pre + "anno_" + oneLetterRes).show();
